<template>
	<view class='wrapper'>
		<scroll-view
			:scroll-y="true"
			:scroll-into-view="currentId"
			:scroll-with-animation="true"
			:enable-back-to-top="true"
			class="scroll_view"
		>
			<view class="yixiang" >
				<view 
					:class="['form_wrapper']"
				>
						<view :class="['qx-from-group']">
							<view class="title">意向车型<text class='text-red'>*</text></view>
							<picker
								@change="carChange($event)" 
								:value="carIdx"
								:range="carList" 
								class="picker"
								range-key="showName"
							>
								<view class="picker">
									<view v-if="paramInfo.orderId">{{paramInfo.carTypeName}}</view>
									<view v-else :class="[!carIdx?'placeholder_style':'']">
										{{carIdx!=null? carList[carIdx].name : '请选择意向车型'}}
									</view>
								</view>
							</picker>
							<view class="img_w flex-row-start">
								<image v-if="paramInfo.carTypeName" src="../../static/clear_black.png" class="img" @click="clearValue(1)"></image>
								<image v-else src="../../static/down_arrow.png" class="img" ></image>
							</view>
						</view>
						<view :class="['qx-from-group']">
							<view class="title">意向4S店<text class='text-red'>*</text></view>
							<input
								placeholder="请选择意向4S店"
								name="input"
								:disabled="true"
								 @click='goSelectShop' 
								 v-model="paramInfo.store4sName"
							 ></input>
							 <!-- 已创建订单 隐藏 图片 -->
							 <view class="img_w flex-row-center">
									<image v-if="paramInfo.store4sName" src="../../static/clear_black.png" class="img" @click="clearValue(2)"></image>
									<image  v-else src="../../static/down_arrow.png" class="img"></image>
							 </view>
						</view>
						<view :class="['qx-from-group','no-border']">
							<view class="title">询价情况</view>
							<input 
								placeholder="请输入已询底价" 
								name="input" 
								v-model="paramInfo.inquiryInfo"
								type="digit"
							></input>
							<view class="unit">万</view>
							<view class="img_w flex-row-center">
								<image v-if="paramInfo.inquiryInfo" src="../../static/clear_black.png" class="img" @click="clearValue(3)"></image>
							</view>
						</view>
				</view>
			</view>
		</scroll-view>
		<view class='btn_wrapper'>
			<view class='cancel' @click="back">
				<button class="cu-btn block lg">
						取消
				</button>
				</view>
			<view class="sure">
				<button class="cu-btn block lg" @click="editOrder" :disabled="btnDisabled">
					保存
				</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				fromType:0,//0 新增 , 1 编辑
				carList:[],//车辆列表
				orderInfo:{}, //订单信息
				carIdx:'',//选择的车型
				paramInfo:{
					orderId:'',// 订单ID Long 必填					carTypeId:'',// 车型ID Integer 必填					carTypeName:'',// 车型名称 String					store4sId:'',// 4S店ID Integer 必填
					store4sName:'',					inquiryInfo:'',//  询价情况  String 必填					modifyUserType:''//修改人类型。1-客户；2-员工； Integer 必填				},
				
				btnDisabled:false,
			};
		},
		methods:{
			carChange(e){
				var _idx=e.detail.value //选的第几个
				this.carIdx=_idx
				this.paramInfo.carTypeId=this.carList[_idx].id
				this.paramInfo.carTypeName=this.carList[_idx].name
			},
			clearValue(type){
				if(type==1){ //清意向车型
					this.paramInfo.carTypeId=null
					this.paramInfo.carTypeName=null
					this.paramInfo.carIdx=null
				}else if(type==2){ //清意向4s店
					this.paramInfo.store4sId=null; // 4s店id
					this.paramInfo.store4sName=null// 4s店名
				}else{
					this.paramInfo.inquiryInfo=null
				}
			},
			goSelectShop(){
				this.perfectNavigateTo({
					url:"/pages/fourShop/fourShop?fromPage=2"
				})
			},
			changeVol(vol){
				if(this.fromType==0){ //只有新绑定可以修改
					this.paramInfo.isVol=vol
				}
			},
			getCarList(){
				this.request("/cartype/spinner").then((res)=>{
					if(res.resultCode==0){
						this.carList=res.data
						for(let i=0,len=this.carList.length;i<len;i++){
							if(this.carList[i].id==this.paramInfo.carTypeId){
								this.carIdx=i
								break;
							}
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			back(){
				uni.showModal({
					title: '提示',
					cancelText:"再看看",//默认是“取消”
					confirmText:"放弃",
					content: '是否放弃本次编辑？',
					success: (res)=>{
						if (res.confirm) {
							uni.navigateBack({
								delta:1
							})
						}
					}
				})
			},
			pageInit(){
				this.getCarList()
				this.paramInfo.orderId=this.orderInfo.orderId
				this.paramInfo.carTypeId=this.orderInfo.carTypeId
				this.paramInfo.carTypeName=this.orderInfo.carTypeName
				this.paramInfo.store4sId=this.orderInfo.store4sId
				this.paramInfo.store4sName=this.orderInfo.store4sName
				this.paramInfo.inquiryInfo=this.orderInfo.inquiryInfo
			},
			editOrder(){
				if(!this.paramInfo.orderId){ uni.showToast({title: '请选择要修改的订单',icon:'none'});return}
				if(!this.paramInfo.carTypeId){ uni.showToast({title: '请选择购买意向的车型',icon:'none'});return}
				if(!this.paramInfo.store4sId){ uni.showToast({title: '请选择购买意向的4S店',icon:'none'});return}
				if(this.paramInfo.inquiryInfo==null || this.paramInfo.inquiryInfo==undefined){
					delete this.paramInfo.inquiryInfo
				}
				this.btnDisabled=true
				this.request("/order/staff/members/modifyOrder",this.paramInfo).then((res)=>{
					if(res.resultCode==0){
						uni.showModal({
							title: '提示',
							content: res.resultMsg,
							showCancel:false,
							success: (res)=>{
								if (res.confirm) {
									uni.navigateBack({
										delta:1
									})
								}
							}
						})
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
						this.btnDisabled=false
					}
				}).catch((err)=>{
					this.btnDisabled=false
				})
			},
		},
		onLoad(params){
			this.orderInfo=JSON.parse(params.orderInfo)
			this.fromType=params.fromType
			if(this.fromType==1){ //客户进来
				this.paramInfo.modifyUserType=1
			}else{
				this.paramInfo.modifyUserType=2
			}
			this.pageInit()
		},
		onShareAppMessage(){
			return {
				title:'卫蓝购',//String	是	分享标题	
				path:'/pages/infomation/index',//String	是	页面 path ，必须是以 / 开头的完整路径。	QQ小程序不支持
				imageUrl:"/static/share.png"
			}
		},
	}
</script>

