		padding-bottom:200upx
		.scroll_view
			max-height:100vh
		.yixiang
			margin-top:30upx
		.form_wrapper
			background:rgba(248,249,252,1);
			border-radius:16upx;
			padding:0upx 30upx
			width:690upx
			margin:0 auto
			position relative
			&:not(:first-child)
				margin-top:30upx
			.delete
				position:absolute
				right:-18upx
				top:-23upx
				padding:10upx
				.delete_img
					width:35upx
					height:35upx
			&.first
				margin-top 30upx
		.register_btn
			margin-top:50upx
			.lg
				height:112upx
				background-color:#1F2334
				letter-spacing 2upx
				color:#fff
		.add
			height:112upx;
			width:690upx
			margin: 0 auto
			display:flex;
			flex-direction:row;
			align-items center;
			background:rgba(248,249,252,1);
			border-radius:19upx;
			padding:0upx 30upx
			justify-content space-between
			margin-top:30upx
			text
				font-size:32upx;
				font-family:PingFangSC-Semibold,PingFang SC;
				font-weight:600;
				color:rgba(30,30,30,1);
			image
				width:28upx
				height:28upx
		.btn_wrapper
			position:fixed
			background:#FFFFFF
			width:750upx
			bottom:0upx
			left:0upx
			right:0upx
			height:155upx
			box-shadow:0px -4px 20px 0px rgba(0,0,0,0.05)
			display:flex
			align-items center
			justify-content center
			z-index:10
			.lg
				height:112upx
			.cancel
				width:280upx
				margin-right:10upx
				button
					background:#FFFFFF
					color:#1F2334
					border-radius:16upx;
					border:2upx solid rgba(203,210,219,1);
			.sure
				width:400upx
				button
					background:rgba(31,35,52,1);
					border-radius:16upx;
					color:#fff
</style>
